.about {
    background-color: $darkgrey;
    box-shadow: 0 25px 15px -15px $aqua inset;

    .about-container {
        padding-top: 100px;
        padding-bottom: 60px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__title {

        &::after {
            content: '1';
        }
    }

    &__descr {
        font-size: 1.5em;
        color: $cream;
        letter-spacing: .1em;
        line-height: 1.5em;

        &::first-letter {
            margin-left: 70px;
        }

        &:not(:last-child) {
            margin-bottom: 35px;
        }
    }
}