.skills {
    background-color: $grey;
    box-shadow: 0 25px 15px -15px $aqua inset;

    .skills-container {
        padding-top: 100px;
        padding-bottom: 60px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__title {
        &::after {
            content: '2';
        }
    }

    &__subtitle {
        font-size: 4em;
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        letter-spacing: 0.25em;

        color: $darkgrey;
        text-shadow: 0px 0px 45px $aqua;
        margin-bottom: 55px;
    }

    .skills-row {
        width: 100%;
        margin-bottom: 55px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }

    .skills-info {
        padding-left: 25px;
        position: relative;
        width: 50%;

        &__title {
            font-size: 1.7em;
            margin-bottom: 15px;
            color: $aqua;
            letter-spacing: 0.15em;

            &::after {
                content: '';
                position: absolute;
                left: 5px;
                width: 4px;
                height: 100%;
                background-color: $aqua;
            }
        }

        .skills-block {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-left: 15px;

            &__code {
                font-size: 1.5em;
                color: $cream;
                line-height: 1.5em;

                &--padding30 {
                    padding-left: 30px;
                }

                &--padding60 {
                    padding-left: 60px;
                }
            }

            &__descr {
                margin-left: 100px;

                font-size: 1.3em;
                letter-spacing: 0.07em;
                color: $darkgrey;
            }

            &__list {
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 80px 0;
                width: 100%;
            }

            &__item {
                height: 70px;
                width: 70px;
                display: flex;
                justify-content: center;
                align-items: center;

                border: 3px solid $white;
                border-radius: 50%;
                background-color: $darkgrey;
                box-shadow: 0px 40px 50px $darkgrey;
                transition: all .2s ease-out .1s;
                cursor: pointer;

                img {
                    transform: scale(1.4);
                }

                &:not(:last-child) {
                    margin-right: 30px;
                }

                &:hover {
                    box-shadow: 0px 40px 50px $darkgrey,
                    0px 0px 30px $aqua,
                    inset 0px 0px 20px $aqua;
                    transform: scale(1.1);
                }
            }

            &--column {
                flex-direction: column;
            }

            &__form-title {
                text-transform: uppercase;
                font-size: 2em;
                color: $cream;
                margin-bottom: 30px;
            }

            &__form {
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 100%;
                max-width: 300px;

                input {
                    background: transparent;
                    outline: none;
                    border: 2px solid $cream;
                    border-radius: 100px;
                    height: 40px;
                    width: 100%;
                    margin-bottom: 30px;
                    padding-left: 15px;
                    font-family: 'Roboto', sans-serif;

                    &[type=button] {
                        width: 50%;
                        margin-bottom: 0;
                        color: $darkgrey;
                        background-color: $cream;
                        font-size: 20px;
                        padding: 0;

                        &:hover,
                        &:focus {
                            background-color: $aqua;
                            border: none;
                        }
                    }

                    &[type=text]:focus,
                    &[type=password]:focus {
                        border-color: $aqua;
                    }

                    &[type=text]:focus ~ label,
                    &[type=password]:focus ~ label,
                    &:not(:placeholder-shown) ~ label {
                        top: -15px;
                        font-size: 13px;
                        color: $aqua;
                    }
                }

                label {
                    position: absolute;
                    top: 10px;
                    left: 15px;
                    transition: all .3s ease-out
                }
            }

            &__group {
                position: relative;
                width: 100%;
            }
        }

        .skills-animation {
            min-width: 150px;
            height: 150px;
            display: flex;
            align-items: center;

            &__flex {
                width: 90%;
                height: 90%;
                border: 3px solid $cream;
                position: relative;
                animation: flex 8s infinite;

                &::before {
                    content: '';
                    width: 30%;
                    height: 30%;
                    background-color: $white;

                    position: absolute;
                    top: 0%;
                    left: 35%;
                    animation: left-flex 8s infinite;
                }

                &::after {
                    content: '';
                    width: 30%;
                    height: 30%;
                    background-color: $white;

                    position: absolute;
                    top: 70%;
                    right: 35%;
                    animation: right-flex 8s infinite;
                }
            }

            &__grid {
                width: 90%;
                height: 90%;
                border: 3px solid $cream;
                position: relative;

                animation: grid 8s infinite;
            }

            &__item {
                width: 35%;
                height: 30%;
                background-color: $white;
                position: absolute;

                &:nth-of-type(1) {
                    animation: grid1 8s infinite;
                    top: 0;
                    left: 0;
                }

                &:nth-of-type(2) {
                    animation: grid2 8s infinite;
                    top: 0;
                    left: 40%;
                }

                &:nth-of-type(3) {
                    animation: grid3 8s infinite;
                    top: 35%;
                    left: 40%;
                }
            }
        }
    }
}