.container {
    max-width: 1330px;
    margin: 0 auto;
    padding: 0 15px;
}

.page__title {
    font-size: 5em;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    color: $white;
    width: 450px;

    position: relative;
    z-index: 0;
    margin-bottom: 110px;

    animation: light 4s infinite;

    &::after {
        position: absolute;
        top: -100px;
        left: -17px;
        z-index: -1;

        font-size: 3em;
        color: $aqua;
        opacity: 0.4;
        text-shadow: none;
    }

    @keyframes light {
        0% {
            text-shadow: none;
        }

        9% {
            text-shadow: none;
        }

        10% {
            text-shadow: 0 0 23px $aqua;
        }

        13% {
            text-shadow: 0 0 23px $aqua;
        }

        14% {
            text-shadow: none;
        }

        25% {
            text-shadow: none;
        }

        26% {
            text-shadow: 0 0 23px $aqua;
        }

        27% {
            text-shadow: none;
        }

        28% {
            text-shadow: 0 0 23px $aqua;
        }

        29% {
            text-shadow: none;
        }

        30% {
            text-shadow: 0 0 23px $aqua;
        }

        31% {
            text-shadow: none;
        }

        80% {
            text-shadow: none;
        }

        81% {
            text-shadow: 0 0 23px $aqua;
        }

        82% {
            text-shadow: none;
        }

        83% {
            text-shadow: 0 0 23px $aqua;
        }

        84% {
            text-shadow: none;
        }

        100% {
            text-shadow: none;
        }
    }
}