.header {
    height: 100vh;
    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.75) 45%, rgba(0, 0, 0, 0.91) 65%), url(../image/header_bg.webp);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    overflow: hidden;
    font-family: 'Ubuntu', sans-serif;

    .header-container {
        min-height: 100%;

        .header-nav {
            display: flex;
            justify-content: end;
            align-items: center;
            height: 180px;

            &__menu {
                color: $aqua;
                font-size: 1.5em;

                display: flex;
                align-items: center;
            }

            &__item {
                letter-spacing: 0.15em;
                font-weight: 700;

                a {
                    transition: all .2s ease;
                    transition-delay: .075s;
                    display: block;
                }

                a:hover {
                    border-bottom: 3px solid $aqua;
                    margin-bottom: 3px;
                    color: $white;
                }
            }

            &__item:not(:last-child) {
                margin-right: 50px;
            }
        }

        .header-hero {
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: relative;

            height: calc(100vh - 180px);


            &::after {
                content: '';
                width: 1000px;
                height: 1000px;
                position: absolute;
                left: 80%;
                top: 37%;

                border: 2px solid $aqua;
                border-radius: 64% 36% 46% 54% / 42% 53% 47% 58%;
                
                animation: heroCircle 15s infinite;

                @keyframes heroCircle {
                    0% {
                        transform: rotate(0deg);
                    }

                    100% {
                        transform: rotate(360deg);
                    }
                }
            }

            .hero-picture {
                background-image: url(../image/hero_pic.webp);
                background-size: cover;
                background-position: center;
                background-repeat: no-repeat;
                min-height: 70%;
                min-width: 30%;

                border-radius: 10px;
                border-right: 15px solid $aqua;
                border-bottom: 15px solid $aqua;
                border-left: 5px solid $aqua;
                border-top: 5px solid $aqua;
                box-shadow: 0 0 20px $aqua;
            }

            .hero-text {
                height: 100%;
                min-width: 60%;

                &__title {
                    font-size: 3.5em;
                    font-family: 'Roboto', sans-serif;
                    font-weight: 700;
                    color: $cream;

                    margin-top: 30%;
                    text-align: right;
                }
            }
        }
    }
}