.works {
    background-color: $darkgrey;
    box-shadow: 0 25px 15px -15px $aqua inset;

    .works-container {
        padding-top: 100px;
        padding-bottom: 60px;
        display: flex;
        align-items: center;
        flex-direction: column;
        min-height: 500px;
    }

    &__title {
        margin-bottom: 150px;

        &::after {
            content: '3';
        }
    }

    &__descr {
        font-size: 50px;
        color: $aqua;
        text-transform: uppercase;
    }
}