.contact {
    background-color: $grey;
    box-shadow: 0 25px 15px -15px $aqua inset;

    .contact-container {
        padding-top: 100px;
        padding-bottom: 60px;
        display: flex;
        align-items: center;
        flex-direction: column;
        min-height: 500px;
    }

    &__title {
        &::after {
            content: '4';
        }
    }

    .contact-block {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        align-items: center;
        justify-content: space-between;

        &__picture {
            background-image: url(/image/contact_bg.webp);
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            min-width: 300px;
            min-height: 400px;
            border-radius: 10px;
            border-right: 15px solid $aqua;
            border-bottom: 15px solid $aqua;
            border-left: 5px solid $aqua;
            border-top: 5px solid $aqua;
            box-shadow: 0 0 20px $aqua;
        }

        &__list {
            min-width: 600px;
            display: flex;
        }

        &__item {
            color: $aqua;
            text-align: center;

            &:not(:last-child) {
                margin-right: 50px;
            }
        }

        &__subtitle {
            font-size: 3em;
            margin-bottom: 15px;
        }

        &__descr {
            font-size: 1.5em;
            color: $cream;
        }
    }
}