html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    font-family: 'Ubuntu';
    font-weight: 400;
    scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
}

p {
    margin: 0;
    padding: 0;
}

 ul, ol {
    margin: 0;
    padding: 0;
    list-style: none;
 }

 a {
    color: inherit;
    text-decoration: none;
 }

 img {
    max-width: 100%;
 }